.link-focusable {
  border-radius: 4px;
}

.link-focusable:focus {
  margin: -1px;
  border: 1px solid var(--color-purple-1);
  outline: 0;
  box-shadow: inset 0 0 0 1px var(--color-pure-white);
}

.btn-focusable:focus-visible, .btn-focusable:hover:focus-visible,
.btn-focusable-border:focus-visible,
.btn-focusable-border:hover:focus-visible {
  border: 2px solid var(--dark-mode-bg-color-2);
  outline: 0;
  box-shadow: inset 0 0 0 2px var(--color-pure-white);
}

.btn-focusable-border:focus-visible, .btn-focusable-border:hover:focus-visible {
  border-radius: 18px;
}

.btn-get {
  text-align: center;
  --bs-btn-font-weight: 600;
  --bs-btn-color: var(--bs-white);
  --bs-btn-bg: var(--btn-bg);
  --bs-btn-border-color: var(--bd-violet-bg);
  --bs-btn-hover-color: var(--bs-white);
  --bs-btn-hover-bg: #7051D9;
  --bs-btn-hover-border-color: transparent;
  --bs-btn-focus-shadow-rgb: var(--bd-violet-rgb);
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: #7759DB;
  --bs-btn-active-border-color: transparent;
  --bs-btn-border-radius: var(--border-radius-1);
  transition: all 0.2s cubic-bezier(0.2, 0, 0, 1);
}
.btn-get:active {
  --bs-btn-border-radius: 16px;
  font-variation-settings: "GRAD" -50;
}
.btn-get:focus-visible {
  margin: -2px;
}

.btn-card {
  --bs-btn-font-weight: 475;
  --bs-btn-color: var(--text-color-dark);
  --bs-btn-bg: var(--bs-gray-200);
  --bs-btn-border-color: transparent;
  --bs-btn-hover-bg: var(--card-btn-hover-bg);
  --bs-btn-hover-border-color: transparent;
  --bs-btn-focus-shadow-rgb: transparent;
  --bs-btn-active-bg: var(--card-btn-hover-bg);
  --bs-btn-active-border-color: transparent;
  border-radius: var(--bs-border-radius-xl);
  transition: border-radius 0.3s cubic-bezier(0.2, 0, 0, 1), background-color 0.3s cubic-bezier(0.2, 0, 0, 1);
  outline: 0;
}
.btn-card .btn-card__title {
  transition: font-variation-settings 0.3s cubic-bezier(0.2, 0, 0, 1);
}
.btn-card:hover {
  outline: 0;
}
.btn-card:hover .btn-card__title {
  font-variation-settings: "GRAD" 50;
}
.btn-card:active, .btn-card:focus, .btn-card:focus-visible {
  border-radius: var(--card-border-radius) !important;
}
.btn-card:active {
  outline: 0 !important;
}
.btn-card:active .btn-card__title {
  font-variation-settings: "GRAD" -50;
}
.btn-card:focus, .btn-card:focus-visible {
  outline: 2px solid var(--text-color-black);
  --bs-btn-bg: var(--card-btn-hover-bg);
}

.btn-menu {
  --bs-btn-color: var(--text-color-dark-2);
  --bs-btn-border-color: transparent;
  --bs-btn-hover-color: var(--text-color-dark);
  --bs-btn-hover-bg: transparent;
  --bs-btn-hover-border-color: transparent;
  --bs-btn-focus-shadow-rgb: none;
  --bs-btn-active-color: var(--bs-btn-hover-color);
  --bs-btn-active-bg: transparent;
  --bs-btn-active-border-color: transparent;
  transition: font-variation-settings 0.2s cubic-bezier(0.2, 0, 0, 1);
}
.btn-menu .btn__icon {
  background-position: center;
  color: inherit;
}
.btn-menu .btn__text {
  color: inherit;
}
.btn-menu .btn-menu__box {
  position: relative;
  overflow: hidden;
  color: inherit;
}
.btn-menu .btn-menu__box::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: transparent;
  z-index: -1;
  border-radius: 100px;
  opacity: 0;
  transform: scaleX(0.32);
  transition-duration: 0.2s;
  transition-property: transform, opacity;
  transition-timing-function: linear;
}
.btn-menu.active .btn-menu__box::after {
  background: var(--bg-color-menu-btn-active);
  opacity: 1;
  transform: scaleX(1);
}
.btn-menu.active .btn__icon {
  font-variation-settings: "FILL" 1, "wght" 400, "opsz" 24;
}
.btn-menu.active .btn__text {
  font-variation-settings: "GRAD" 125;
}
.btn-menu:hover .btn__text {
  font-variation-settings: "GRAD" 50;
}
.btn-menu:hover .btn__icon {
  font-variation-settings: "wght" 600, "opsz" 24;
}
.btn-menu:hover .btn-menu__box {
  background: var(--bg-color-linear-3);
}
.btn-menu:hover.active .btn__text {
  font-variation-settings: "GRAD" 125;
}
.btn-menu:hover.active .btn__icon {
  font-variation-settings: "FILL" 1, "wght" 600, "opsz" 24;
}
.btn-menu:active .btn-menu__box {
  background: var(--bg-color-menu-btn-active-2);
}
.btn-menu:active .btn__icon {
  font-variation-settings: "wght" 300, "opsz" 24;
}
.btn-menu:active .btn__text {
  font-variation-settings: "GRAD" -50;
}

