@keyframes collapseAnimation {
  0% {
    opacity: 0;
    width: 100%;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}
.collapse {
  background-color: transparent;
  opacity: 1;
}
.collapse .collapse-wrapper {
  opacity: 0;
}
.collapse:not(.show) {
  display: none;
  opacity: 0;
}

.collapsing {
  opacity: 1;
  animation-name: collapseAnimation;
  animation-duration: 3s;
  height: 0;
  overflow: hidden;
}
.collapsing.collapse-horizontal {
  width: 100%;
  opacity: 0;
  height: auto;
}
.collapsing.collapse-horizontal .collapse-wrapper {
  opacity: 1;
}

.collapse--hidden {
  opacity: 0;
}

